/* Extra 'Normalization' */
* {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}

/* html {
  scroll-behavior: smooth;
} */

/* Buttons */
button {
  border: none;
  background: none;
}
i {
  cursor: pointer;
}
.btn {
  cursor: pointer;
  transition: all 0.2s;
  padding: 7px 10px;
  border: 1px solid;
}
.btn-price {
  border-right: none;
}

.btn-learn-more {
  border-right: none;
}
.btn-add-to-cart {
  border-left: none;
}
.btn-block {
  width: 100%;
  border: none;
  margin-top: px;
}

.preview-footer .btn {
  box-shadow: none;
  font-size: 1rem;
}

/* Navigation/Search */

.top-nav {
  position: fixed;
  top: 20px;
  left: 47.5%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 32px;
  width: 88vw;
  height: 50px;
}

.btn-cart {
  cursor: pointer;
  position: absolute;
  right: -20px;
  top: -5px;
  border-radius: 100%;
  width: 2rem;
  height: 2rem;
  padding: 13px;
  box-sizing: content-box;
}

.btn-cart i {
  -webkit-transform: translate(-7%, 0%);
          transform: translate(-7%, 0%);
}

.btn-cart > i > .car-items-num {
  font-size: 0.9rem;
  position: absolute;
  top: 6px;
  left: 17px;
  text-shadow: none;
  font-weight: 400;
}

.nav-logo {
  float: left;
}

.nav-logo {
  height: 50px;
  padding-left: 10px;
}

.nav-logo > img {
  height: 100%;
}

/* Checkout Modal */

.checkout-body {
  z-index: 2;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: scroll;
  padding: 20px 25px;
  width: 80vw;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 0px 1px 150px;
}

.checkout-body .header {
  width: 90%;
  margin: auto;
  text-align: center;
  margin-bottom: 10px;
}
.items-table {
  width: 100%;
}

.items-table table {
  width: 100%;
}

.items-table > table {
  font-size: 0.85rem;
}
.items-table th {
  text-align: left;
  padding-left: 2px;
  padding-bottom: 3px;
}

.items-table td:first-child {
  font-weight: 400;
  padding-right: 10px;
}

.items-table td:nth-child(2) {
  border-left: solid 1px;
  padding-left: 10px;
  width: 25%;
}
.items-table td {
  border-bottom: solid 1px;
  padding-bottom: 5px;
  padding-top: 5px;
}
/* Thumbnails View */

.container {
  display: flex;
  flex-wrap: wrap;
  padding-top: 35px;
  margin: auto;
  box-sizing: border-box;
}

.container .feed-thumbnail-wrap {
  margin: auto;
  display: inline-block;
  box-sizing: border-box;
}

.feed-thumbnail-wrap {
  cursor: pointer;
  width: 50%;
  box-sizing: border-box;
  transition: all 0.2s;
}

.feed-thumbnail-wrap > span.thumbnail-name {
  position: relative;
  top: 200px;
  right: -15%;
  border: 1px solid;
  padding: 5px;
  width: 65%;
  display: inline-block;
}

.feed-thumbnail {
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
}
/* .container > .col-1 img {
  width: 47vw;
  height: 47vw;
  margin: 1.5vw;
} */

/* Product Preview Modal */

.modal {
  z-index: 1;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  /* transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s; */
}

.product-preview-modal {
  padding: 25px;
  position: fixed;
  width: 80vw;
  max-height: 90vh;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 0px 0px 150px;
}
.product-preview-modal > i {
  font-size: 1.2rem;
}
.product-preview-modal .preview-header {
  width: 90%;
}

.big-thumbnail {
  transition-delay: 0.5s;
  transition: all 0.5s;
}
.big-thumbnail {
  padding: 10px 0 10px 0px;
}
.big-thumbnail img {
  width: 270px;
  margin-left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.preview-footer {
  margin-top: 10px;
  text-align: center;
}

/* ProductDetailView.js */

.product-detail-container {
  box-sizing: border-box;
  padding: 15px;
}
.breadcrumbs {
  margin-top: 50px;
  padding-left: 30px;
  font-size: 0.9rem;
  margin-bottom: 5px;
}

.product-name {
  margin-top: 15px;
  padding-left: 5px;
  padding-right: 5px;
  transition: all 0.2s;
}

.product-image img {
  width: 100%;
}
.info-bar {
  font-size: 0.95rem;
  display: flex;
  justify-content: space-evenly;
  margin-top: 13px;
  padding-top: 7px;
  padding-bottom: 6px;
  width: 100%;
  border: 1px solid;
  border-left: none;
  border-right: none;
}
.info-bar .price {
  display: inline-block;
}

.info-bar .stock-info {
  display: inline-block;
}
.product-description {
  width: 100%;
  margin-top: 15px;
  padding-left: 5px;
  padding-bottom: 10px;
  margin-bottom: 30px;
}
.product-description em {
  font-size: 0.9rem;
  margin-top: 15px;
  margin-right: 15px;
  float: right;
}

.product-description-empty {
  margin-top: 0px;
  padding-left: 5px;
  margin-bottom: 7px;
}

.product-description-empty > em {
  font-size: 0.9rem;
  margin-top: 10px;
  margin-left: 3px;
  margin-right: 3px;
  display: inline-block;
  float: none;
}
.suggested-items {
  width: 100%;
}
.suggested-item {
  width: 33%;
  box-sizing: border-box;
  display: inline-block;
  padding: 10px;
}

.suggested-item img {
  transition: 0.2s all;

  width: 100%;
}
.sales-rep {
  margin-top: 13px;
  padding-left: 3px;
}

.sales-rep > h3 {
  font-size: 0.95rem;
  font-weight: 400;
  margin-bottom: 4px;
}
.sales-rep-table {
  float: left;
}
.sales-rep-table table {
  font-size: 0.9rem;
}
.sales-rep-table table {
  width: 90vw;
}
img.sales-rep-img {
  width: 90px;
  margin-left: 15px;
  margin-top: 5px;
}
.sales-rep-table td:first-child {
  font-weight: 400;
  padding-right: 10px;
}

.sales-rep-table td:nth-child(2) {
  border-left: solid 1px;
  padding-left: 10px;
  width: 70%;
}
.sales-rep-table td {
  border-bottom: solid 1px;
  padding-bottom: 5px;
  padding-top: 5px;
}

* {
  font-family: Roboto, Helvetica, sans-serif;
  font-weight: 300;
}
h2 {
  font-family: "Cinzel";
  font-weight: 400;
  font-size: 1.3rem;
}

.btn {
  font-size: 0.9rem;
  font-weight: 300;
  text-transform: uppercase;
}
.feed-thumbnail-wrap > span.thumbnail-name {
  font-family: "Cinzel", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 0.8rem;
}

:root {
  --brand1: #fff426;
  --brand2: #f8b500;
  --white: white;
  --charcoal: rgb(55, 55, 55);
  --lightgrey: lightgray;
  --darkgrey: darkgrey;
}
.body,
.html,
.main {
  background: white;
}
a:visited {
  color: rgb(55, 55, 55);
  color: var(--charcoal);
}

/* Buttons */
.btn {
  background: white;
  border-color: #d6d6d6;
}

.btn-learn-more {
  color: rgb(45, 45, 45);
}

.btn-learn-more:hover {
  box-shadow: darkgrey 0px 0px 10px;
  box-shadow: var(--darkgrey) 0px 0px 10px;
  color: white;
  color: var(--white);
  text-shadow: 0px 0px 2px lightgray;
  text-shadow: 0px 0px 2px var(--lightgrey);
}
.btn-add-to-cart {
  color: black;
  background-color: #fff426;
  background-color: var(--brand1); /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    45deg,
    #fff426 -1%,
    #f8b500 100%
  );
  background: linear-gradient(
    45deg,
    var(--brand1) -1%,
    var(--brand2) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fff426', endColorstr='#f8b500',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

  box-shadow: lightgray 0px 0px 5px;

  box-shadow: var(--lightgrey) 0px 0px 5px;
}

.btn-add-to-cart:hover {
  box-shadow: darkgrey 0px 0px 10px;
  box-shadow: var(--darkgrey) 0px 0px 10px;
  color: white;
  color: var(--white);
  text-shadow: 0px 0px 2px lightgray;
  text-shadow: 0px 0px 2px var(--lightgrey);
}
.btn-add-to-cart:disabled {
  background: lightgray;
  background: var(--lightgrey);
  color: rgb(55, 55, 55);
  color: var(--charcoal);
  box-shadow: none;
}

/* Check-Out Modal */
.checkout-body {
  color: black;
  background: white;
  background: var(--white);
}
.items-table > table td,
.items-table td:nth-child(2) {
  border-color: lightgray;
  border-color: var(--lightgrey);
}
.items-table i {
  float: right;
  color: darkgrey;
  color: var(--darkgrey);
  transition: all 0.2s;
}
.items-table i:hover {
  color: rgb(199, 0, 0);
}

/* Navigation/Search */
.top-nav {
  color: white;
  z-index: 1;
  background-color: #fff426;
  background-color: var(--brand1); /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    45deg,
    white 35%,
    #f8b500 85%
  );
  background: linear-gradient(
    45deg,
    var(--white) 35%,
    var(--brand2) 85%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fff426', endColorstr='#f8b500',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.534);
}

.btn-cart {
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.658);
  background-color: #fff426; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    45deg,
    #fff426 -1%,
    #f8b500 100%
  );
  background: linear-gradient(
    45deg,
    var(--brand1) -1%,
    var(--brand2) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fff426', endColorstr='#f8b500',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.534);
}
.btn-cart > i > .car-items-num {
  color: rgb(55, 55, 55);
  color: var(--charcoal);
}
/* ThumbnailView */
.feed-thumbnail-wrap > span.thumbnail-name {
  background-color: rgba(255, 255, 255, 0.78);
  border-color: lightgrey;
}

/* Product Preview Modal */
.modal {
  background-color: rgba(0, 0, 0, 0);
}
.product-preview-modal {
  background: white;
  /* border-color: grey; */
  box-shadow: black;
}

/* ProductDetailView.js */
.breadcrumbs {
  color: rgb(55, 55, 55);
  color: var(--charcoal);
}
.breadcrumbs span {
  color: rgb(55, 55, 55);
  color: var(--charcoal);
}
.info-bar {
  border-color: lightgrey;
}
.info-bar .stock-info {
  border-right-color: lightgray;
  border-right-color: var(--lightgrey);
}
.price i {
  color: rgb(55, 55, 55);
  color: var(--charcoal);
}
.divider {
  color: rgb(55, 55, 55);
  color: var(--charcoal);
}

.product-description {
  border-bottom-color: lightgray;
  border-bottom-color: var(--lightgrey);
}

.sales-rep-table td:nth-child(2) {
  border-left-color: lightgrey;
}
.sales-rep-table td {
  border-bottom-color: lightgrey;
}

@media screen and (min-width: 950px) {
  .body {
    background: black;
  }
  .main {
    background: rgb(233, 233, 233);
    height: 100%;
  }
  .product-preview-modal > i {
    font-size: 1.5rem;
  }
  .big-thumbnail img {
    height: 35vh;
    width: auto;
  }
  .product-preview-modal {
    width: 60vw;
  }
  .preview-footer .btn {
    font-size: 1.2rem;
    font-weight: 300;
    margin-top: 10px;
  }
  .container {
    display: flex;
    flex-wrap: wrap;
    padding-top: 75px;
    margin: auto;
    width: 70vw;
    background: white;
    height: 100%;
  }
  .product-detail-container {
    display: inline-block;
    box-sizing: border-box;
    height: 100vh;
    margin-bottom: 50px;
  }

  .container .feed-thumbnail-wrap {
    margin: auto;
    display: inline-block;
  }

  .feed-thumbnail-wrap:nth-child(1) {
    width: 60%;
  }

  .feed-thumbnail-wrap {
    width: 25%;
    padding: 5px;
  }
  .feed-thumbnail-wrap:hover {
    width: 24%;
  }
  .feed-thumbnail-wrap:nth-child(1):hover {
    width: 59.5%;
  }
  .product-preview-modal {
    box-shadow: 0px 0px 250px;
  }

  .breadcrumbs {
    display: block;
    margin-top: 8px;
    padding-left: 25px;
    font-size: 1.1rem;
    margin-bottom: 5px;
  }
  .product-info {
    width: 45%;
    display: inline-block;
    box-sizing: content-box;
    box-sizing: initial;
    vertical-align: 0px;
  }
  .product-image {
    max-width: 45%;
    display: inline-block;
    vertical-align: 50px;
  }
  .product-image img {
    width: 95%;
  }
  .top-nav {
    width: 75vw;
    left: 50%;
  }

  .search {
    padding: 20px;
    top: -10px;
  }
  .product-preview-modal {
    max-width: 600px;
  }
  .product-preview-modal .preview-header {
    padding: 5px;
    width: 80%;
  }
  .product-description {
    margin-bottom: 50px;
  }
  h2 {
    font-size: 1.7rem;
  }
  .btn {
    font-size: 1.2rem;
  }
  .preview-desc p {
    font-size: 1.1rem;
  }

  .checkout-body {
    width: 45vh;
    box-shadow: 0px 0px 300px rgb(55, 55, 55);
  }

  .items-table {
    width: 100%;
  }

  .items-table table {
    width: 100%;
  }

  .sales-rep-table {
    width: 100%;
  }
  .sales-rep-table table {
    width: 100%;
  }

  .suggested-item img {
    width: 100%;
  }
  .suggested-item img:hover {
    width: 95%;
  }
  .feed-thumbnail-wrap > span.thumbnail-name {
    position: relative;
    top: 120px;
    right: -15%;
    padding: 5px;
    width: 70%;
    display: inline-block;
    font-size: 0.7rem;
  }
  .feed-thumbnail-wrap:nth-child(1) > span.thumbnail-name {
    top: 300px;
    right: -20%;
    width: auto;
    font-size: 0.95rem;
  }
}

@media screen and (min-width: 1340px) {
  .container {
    display: flex;
    flex-wrap: wrap;
    padding-top: 75px;
